@import url(https://fonts.googleapis.com/css?family=Ledger|Nixie+One);
* {
	box-sizing: border-box;
}
:root {
	--color__light: #f1f2f2;
	--color__dark: #250a04;
	--color__base-red: rgb(160, 61, 41);
	--color__complementary: rgb(41, 140, 160);
	--ease-in-out-circle: cubic-bezier(0.785, 0.135, 0.150, 0.860);
}
@keyframes bg {
0% {
		background-position: 0 50%
}
50% {
		background-position: 100% 50%
}
100% {
		background-position: 0 50%
}
}
::selection {
	text-shadow: none;
	color: var(--color__dark);
	background-color: var(--color__light);
}
html {
	-webkit-tap-highlight-color: transparent;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
	touch-action: manipulation;
}
html,
body {
	min-height: 100%;
}
body {
	font-family: BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;
	font-size: 100%;
	line-height: 1.5;
	min-width: 320px;
	background-image: linear-gradient(
			135deg,
			rgba(112, 41, 26, 1) 0%,
			rgba(160, 61, 41, 1) 30%,
			rgba(188, 70, 47, 1) 60%,
			rgba(239, 89, 59, 1) 90%);
	background-size: 400% 400%;
	animation: bg 25s ease-in-out infinite alternate both;
	color: var(--color__dark);
	margin: 0;
}
.fonts-loaded body {
	font-family: 'Ledger', serif;
}
h1, h2, h3, h4 {
	font-weight: 300;
}
h1 {
	font-family: cursive;
}
.fonts-loaded h1 {
	font-family: 'Nixie One', cursive;
}
button {
	padding: 0;
	border: 0;
	background: none;
	cursor: pointer;
}
button:focus {
	outline: none;
}
svg {
	display: inline-block;
	transition: 0.25s transform var(--ease-in-out-circle);
}
path,
rect {
	transition: fill, stroke-dash-array, stroke-dash-offset 0.2s var(--ease-in-out-circle);
}
figure {
	margin: 0;
}
a {
	color: inherit;
	border-bottom: 1px solid;
	text-decoration: none;
	position: relative;
	transition: border-bottom-color 0.25s ease-out;
}
a::after {
	content: '';
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	background-color: rgba(255,255,255,0.1);
	transition: opacity 0.2s ease-out, transform 0.3s var(--ease-in-out-circle);
	opacity: 0;
}
.no-touchevents a:hover::after {
	opacity: 1;
	transform: rotate(-2.3deg);
}
.no-touchevents a:hover {
	border-bottom-color: rgba(0,0,0,0);
}
iframe {
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
.video-wrapper {
	position: relative;
	padding-bottom: 56.25%;
	height: 0;
}
.page-wrap {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	padding: 2vh 3vw;
}
@media screen and (min-width: 72em) {
.page-wrap {
		padding-left: 5vw;
}
}
.v-enter-active,
.v-leave-active {
	transition: opacity 1s var(--ease-in-out-circle);
}
.v-enter,
.v-leave-active {
	opacity: 0;
}


